* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}
.App {
  text-align: center;
  font-family: monospace;
  /* background-color: rgba(36, 140, 232, 0.9); */
}
.App::before {
  content: " ";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  /* opacity: 0.6; */
  background-color: #079992;
  z-index: -999;
}
.section_heading {
  margin: 40px;
}
p {
  line-height: 2;
  margin: 10px auto;
}
/* navbar Section */
nav {
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  padding: 14px 15px;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 999;
  /* margin-bottom: 20px; */
}
nav > * {
  color: white;
}
.nav_catagory {
  padding: 10px 2px;
  transition: 200ms;
  font-size: 16px;
}
.nav_catagory:hover {
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 7px;
}
.nav_catagory::before {
  content: "<";
  visibility: hidden;
}
.nav_catagory:hover::before {
  content: "<";
  visibility: visible;
  animation: rotate 20s linear infinite;
}
.nav_catagory::after {
  content: "/>";
  visibility: hidden;
}
.nav_catagory:hover::after {
  content: "/>";
  visibility: visible;
}
.content_wrapper {
  display: flex;
}
.section_wrapper {
  display: flex;
  gap: 15px;
}
a {
  text-decoration: none;
}

/* About Section */
/* div#about {
  border: 1px solid blue;
  display: flex;
  align-items: center;
  padding: 10px;
} */

/* div#about > div:nth-child(2) {
  padding: 10px;
} */

div#about {
  /* border: 1px solid red; */
  display: grid;
  background-color: rgba(0, 0, 0, 0.7);
  /* border-radius: 17px; */
  grid-template-areas:
    "image name name"
    "image information information";
  padding: 25px;
  column-gap: 30px;
}
div#about > div:nth-child(2) {
  border: 1px solid blue;
  grid-area: name;
  margin-top: auto;
  margin-bottom: 10px;
  animation: top_to_bottom 0.5s linear;
  line-height: 1.7;
  /* text-align: left; */
}
div#about > p {
  /* border: 1px solid violet; */
  /* text-align: left; */
  grid-area: information;
  /* margin-bottom: auto;
  margin-top: 5px;
  line-height: 2; */
  animation: bottom_to_top 0.5s linear;
}

/* Avatar section */
#avatar {
  border-radius: 50%;
  overflow: hidden;
  animation: left_to_right 0.5s linear;
}
#avatar_container {
  /* overflow: hidden; */
  grid-area: image;
  margin: auto;
}
#avatar_image {
  width: 100%;
  height: 100%;
}

/* Skills Section */
#skillsGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  /* border: 1px solid pink; */
  gap: 3vw;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 3%;
  background-color: rgba(0, 0, 0, 0.7);
}
/* .skill_logo_container {
  overflow: hidden;
} */
.skill_logo_container > img {
  height: 100px;
  transition: 200ms linear;
}
.react_logo {
  animation: rotate 10s linear infinite;
}
.logo_component {
  background-color: white;
  border: 4px solid #079992;
  padding: 10px 0;
  border-radius: 17px;
  animation: left_to_right 1s linear;
  transition: 100ms linear;
}
.logo_component:hover .skill_logo_container > img {
  transform: scale(1.1);
}
.logo_component:hover {
  box-shadow: 0px 0px 0px 4px #f0932b;
}
.logo_component > p {
  color: black;
  font-weight: bold;
}

/* Project section */
#project_container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3vw;
  width: 97%;
  margin: 20px auto;
}
#project_container > div {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 2vw;
  border-radius: 1vw;
}
.project_template {
  text-align: left;
}
.project_template > h4 {
  font-size: 26px;
  margin: 10px auto;
}
.project_cover_image {
  width: 100%;
  border-radius: 1vw;
}
.tech_stack > img {
  width: 50px;
}
#project_container > div > a > button {
  background-color: rgb(20, 155, 255);
  padding: 5px;
  border-radius: 7px;
  border: none;
  margin: 10px;
  cursor: pointer;
  font-size: 20px;
}
.project_pages_box {
  background-color: rgb(20, 155, 255);
  border-radius: 7px;
  padding: 2px;
  margin: 2px;
}
/* GitHub Section */
#github_section {
  margin: 30px 0;
}
#github_section > div {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 20px;
  /* border-radius: 17px; */
}
.stats_container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 20px;
}
.stats_container > img {
  max-width: 100%;
  margin: 10px auto;
}

/* Contact Section */
#contact_section {
  padding: 30px 0;
  background-color: rgba(0, 0, 0, 0.7);
}
.resume_buttons > a {
  color: black;
  text-decoration: none;
  background-color: pink;
  padding: 5px;
  border-radius: 7px;
  margin: 5px;
  font-weight: bold;
}
#contact_section > span {
  padding: 5px;
  font-size: 18px;
}
#contact_section > * {
  margin: 20px auto;
}
#contact_section > a {
  /* border: 1px solid black; */
  color: red;
  font-size: 50px;
}

@keyframes rotate {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(-360deg);
  }
}

@keyframes left_to_right {
  from {
    transform: translateX(-50%);
  }
}

@keyframes top_to_bottom {
  from {
    transform: translateY(-50%);
  }
}

@keyframes bottom_to_top {
  from {
    transform: translateY(100%);
  }
}

@media screen and (max-width: 650px) {
  div#about {
    padding: 10px;
    grid-template-areas:
      "image name name"
      "information information information";
  }
  div#about > div:nth-child(2) {
    grid-area: name;
    margin: auto;
  }
  #skillsGrid {
    grid-template-columns: repeat(3, 1fr);
  }
  #project_container {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media screen and (max-width: 570px) {
  nav {
    display: block;
    /* border: 1px solid black; */
  }
  .nav_catagory {
    font-size: 22px;
  }
  .section_wrapper {
    display: none;
    width: 100%;
  }
  .open_menu_bar {
    display: block;
    position: absolute;
    left: 0;
    top: 53px;
    background-color: rgba(0, 0, 0, 0.7);
    transition: 200ms;
  }
  .open_menu_bar > a {
    display: block;
  }
  div#about {
    grid-template-areas:
      "image "
      "name"
      "information";
  }
  #avatar {
    height: 50vw;
    width: 50vw;
  }
}
